body{
    height: 100%;
}

.columns{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 20vw;    
    /* make it 100% */
    background-color: black;
}



.sidebar{
    width: 100%;
    
}
.Content{
    align-items: center;
    justify-content: center;
}
.rowfluid {
    position: fixed;
    overflow: auto; 
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start; 
    white-space: nowrap;
    height: 90.5vh;
    width: 100%;
}

.rightDiv {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
}

