body{
    height: 100%;
}

.rowfluid {
    position: fixed;
    overflow: auto; 
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    white-space: nowrap;
    height: 90.5vh;
}

.columns{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100%;
    width: 20vw;    
    background-color: black;
}

.sidebar{
    margin-top: 30px;
    min-width: 100%;

}
.formGroup {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 450px;
    margin-left: 2em;
}

.rightDiv {
    min-width: 0;
    width: 100%;
    margin-top: 30px;

}

@media only screen and (max-width:635px){
    .rowfluid{
        width: 100%;
    }
    .rowfluid .columns{
        width: 100%;
    }
}
